import React from 'react';
import { navigate } from 'gatsby';

import Button from './Button';
import Container from './Container';

import { screen } from '../constants';

export default function BlockText({
  pretitle = '',
  title = '',
  desc = '',
  primaryButtonTo = '/',
  primaryButtonText = '',
  secondaryButtonTo = '/',
  secondaryButtonText = '',
  textAlign = 'center',
}) {
  function handlePrimaryButtonClick() {
    navigate(primaryButtonTo);
  }

  function handleSecondaryButtonTextClick() {
    navigate(secondaryButtonTo);
  }

  return (
    <section className="block-text">
      <div className="block-text__wrap">
        <div>
          {pretitle && <div className="app__pretitle">{pretitle}</div>}
          {title && <h1 className="app__title">{title}</h1>}
          <div className="app__desc">{desc}</div>
          <div className="app__actions">
            {primaryButtonText && (
              <Button onClick={handlePrimaryButtonClick} primary large hasArrow>
                {primaryButtonText}
              </Button>
            )}
            {secondaryButtonText && (
              <Button large onClick={handleSecondaryButtonTextClick}>
                {secondaryButtonText}
              </Button>
            )}
          </div>
        </div>
      </div>
      <style jsx>{`
        .block-text__wrap {
          display: flex;
          flex-wrap: wrap;
          justify-content: space-between;
          align-items: flex-start;
          text-align: ${textAlign};
        }
        .block-text__wrap > div {
          flex: 1;
        }
        .block-text__wrap .app__desc,
        .block-text__wrap .app__title {
          max-width: 700px;
          margin-left: auto;
          margin-right: auto;
          margin-bottom: 1rem;
        }

        .block-text__wrap .app__desc {
          font-size: 1.25rem;
          line-height: 1.6em;
          margin-bottom: 1rem;
          margin-block-end: 1rem;
        }

        .block-text__wrap .app__desc p {
          margin-bottom: 1rem;
          margin-block-start: 1rem;
          margin-block-end: 1rem;
        }

        .app__actions {
          text-align: center;
        }

        @media (max-width: ${screen.tabletMax}px) {
          .block-text__wrap {
            align-items: center;
          }
          .block-text__wrap > div {
            flex: initial;
            margin: 0;
          }
          .block-text__wrap {
            flex-direction: column-reverse;
          }
          .app__title {
            line-height: 56px;
          }
        }
      `}</style>
      <style jsx global>{`
        .block-text__wrap .app__desc p {
          margin-bottom: 1rem;
        }
      `}</style>
    </section>
  );
}
